  font-family: 'CustomFont';
  src: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) format('truetype');
  font-weight: normal;
  font-style: normal;
}

body {
  font-family: 'CustomFont', Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f8f8f8;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
  background-repeat: repeat;
}

header {
  background-color: #2c3e50;
  padding: 15px;
  text-align: center;
}

nav button {
  margin: 0 15px;
  padding: 10px 20px;
  cursor: pointer;
  background-color: #2ace97;
  color: white;
  border: none;
  border-radius: 5px;
  font-size: 16px;
}

nav button:hover {
  background-color: #2980b9;
}

#content {
  max-width: 900px;
  margin: 30px auto;
  padding: 20px;
  background-color: rgba(255, 255, 255, 0.728);
  border-radius: 8px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.home-content {
  text-align: center;
}

.home-content h1 {
  color: #2c3e50;
  font-size: 36px;
  margin-bottom: 20px;
}

.home-content p {
  color: #555;
  font-size: 18px;
  line-height: 1.6;
}

.home-content .hero-image {
  width: 100%;
  height: auto;
  object-fit: cover;
  border-radius: 8px;
  margin: 20px 0;
}

.menu-content h1 {
  color: #2c3e50;
  font-size: 36px;
  text-align: center;
  margin-bottom: 20px;
}

.menu-content .menu-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
}

.menu-content .menu-item {
  background-color: #f1f1f1;
  padding: 15px;
  border-radius: 8px;
  text-align: center;
}

.menu-content .menu-item img {
  width: 100%;
  height: 250px;
  object-fit: fill;
  border-radius: 5px;
  margin-bottom: 10px;
}

.menu-content .menu-item h3 {
  margin: 0;
  color: #333;
  font-size: 20px;
}

.menu-content .menu-item p {
  margin: 5px 0 0;
  color: #777;
  font-size: 16px;
}

.contact-content h1 {
  color: #2c3e50;
  font-size: 36px;
  text-align: center;
  margin-bottom: 20px;
}

.contact-content .contact-info {
  display: flex;
  flex-direction: column;
  gap: 15px;
  max-width: 500px;
  margin: 0 auto;
}

.contact-content .contact-item {
  background-color: #f1f1f1;
  padding: 10px;
  font-family: 'CustomFont', Arial, sans-serif;
  border-radius: 5px;
  font-size: 16px;
  color: #333;
}
